/** @format */
//import './wdyr'
import { ApolloClient, ApolloLink, ApolloProvider } from '@apollo/client';
import { ThemeProvider } from '@mui/material/styles';
import { onError } from 'apollo-link-error';
import { createUploadLink } from 'apollo-upload-client';
import { LocalStorageWrapper, persistCache } from 'apollo3-cache-persist';
import React from 'react';
import 'react-confirm-alert/src/react-confirm-alert.css';
import ReactDOM from 'react-dom';
import 'react-image-gallery/styles/scss/image-gallery.scss';
import { ModalProvider } from 'react-modal-hook';
import { BrowserRouter as Router } from 'react-router-dom';
import App from './App';
import './App.css';
import { cache } from './cache';
import './GridStyles.scss';
import './index.css';
import reportWebVitals from './reportWebVitals';
import { theme } from './theme';

persistCache({
	cache,
	storage: new LocalStorageWrapper(window.sessionStorage),
});

const { REACT_APP_API_ENDPOINT, REACT_APP_API_PORT } = process.env;

export const client = new ApolloClient({
	link: ApolloLink.from([
		onError(({ graphQLErrors, networkError }) => {
			if (graphQLErrors)
				graphQLErrors.map(({ message, locations, path }) =>
					console.log(
						`[GraphQL error]: Message: ${message}, Location: ${locations}, Path: ${path}`,
					),
				);
			if (networkError) console.log(`[Network error]: ${networkError}`);
		}),
		createUploadLink({
			// uri: 'http://localhost:5000/graphql',
			// uri:    `http://localhost:5000/graphql`
			//uri: REACT_APP_API_HOST+'/graphql'
			//uri: REACT_APP_API_ENDPOINT,
            uri: "/graphql"
		}),
	]),

	cache,
});

function initialise() {
	if (cssHasLoaded('ag-theme-custom-react')) {
		ReactDOM.render(
			<Router>
				<ApolloProvider client={client}>
					<ThemeProvider theme={theme}>
						<ModalProvider>
							<App />
						</ModalProvider>
					</ThemeProvider>
				</ApolloProvider>{' '}
				,
			</Router>,
			document.getElementById('root'),
		);
	} else {
		setTimeout(initialise, 100);
	}
}

