/** @format */

import { CircularProgress } from '@mui/material';
import React, { lazy, Suspense } from 'react';
import { Route, Switch } from 'react-router-dom';
import LoginForm from './components/forms/LoginForm';
import NavigationDrawer from './components/navigation/NavigationDrawer';
import PageTitle from './components/navigation/PageTitle';

const PeriodValueEnquiry = lazy(() =>
	import('./components/enquiries/financials/PeriodValueEnquiry'),
);

const ApplicationEnquiry = lazy(() =>
	import('./components/enquiries/applications/ApplicationEnquiry'),
);

const OrdersEnquiry = lazy(() =>
	import('./components/enquiries/orders/OrdersEnquiry'),
);

const Home = lazy(() => import('./components/home/Home'));
const Orderheaders = lazy(() =>
	import('./components/orderheaders/Orderheaders'),
);
const OrderAdmin = lazy(() => import('./components/order-admin/OrderAdmin'));
const AdminHome = lazy(() => import('./components/admin/AdminHome'));
const TestComponent = lazy(() =>
	import('./components/test-components/TestComponent'),
);
const ImportOrderDetail = lazy(() =>
	import('./archive/ImportOrderDetailExcel'),
);
const Enquiries = lazy(() => import('./components/enquiries/Enquiries'));
const Analysis = lazy(() => import('./archive/financials_archive/Analysis'));
const ContractDashboard = lazy(() =>
	import('./components/dashboard/ContractDashboard'),
);
const ApplicationAdmin = lazy(() =>
	import('./components/application-admin/ApplicationAdmin'),
);

const ReportSelection = lazy(() =>
	import('./components/reports/ReportSelection'),
);

function App() {
	return (
		<div className='App'>
			<NavigationDrawer>
				<PageTitle />
				<Suspense fallback={<CircularProgress />}>
					<Switch>
						<Route path='/' exact>
							<Home />
						</Route>
						<Route path='/test' exact>
							<TestComponent />
						</Route>
						<Route path='/projects' exact>
							<Orderheaders />
						</Route>
						<Route path='/orders/import/:orderId' exact>
							<ImportOrderDetail />
						</Route>
						<Route path='/projects/admin'>
							<OrderAdmin />
						</Route>
						<Route path='/admin'>
							<AdminHome />
						</Route>
						<Route path='/applications'>
							<ApplicationAdmin />
						</Route>
						<Route path='/dashboard'>
							<ContractDashboard />
						</Route>
						<Route path='/analysis'>
							<Analysis />
						</Route>
						<Route path='/enquiries' exact>
							<Enquiries />
						</Route>
						<Route path={'/enquiry/applications'}>
							<ApplicationEnquiry />
						</Route>
						<Route path={'/enquiry/projects'}>
							<OrdersEnquiry />
						</Route>
						<Route path={'/enquiry/period-financials'}>
							<PeriodValueEnquiry />
						</Route>

						<Route path={'/reports'} exact>
							<ReportSelection />
						</Route>
						{/*<Route path={'/reports/applications/listing'} exact>
                            <ApplicationsTableReport/>
                        </Route>*/}
						<Route path={'/login'}>
							<LoginForm />
						</Route>
					</Switch>
				</Suspense>
			</NavigationDrawer>
		</div>
	);
}

export default App;
